import { DatabaseService } from '@app/services/database.service';
import { TYPES } from '@app/types';
import { HttpStatus } from '@app/http-status';
import { NextFunction, Request, Response, Router } from 'express';
import * as fs from 'fs';
import { inject, injectable } from 'inversify';
import * as multer from 'multer';
import * as path from 'path';
import { MetaData } from '@common/communication/metadata';
import { STORAGE_DIR } from '@app/constants';

@injectable()
export class DatabaseController {
    router: Router;
    upload: multer.Multer;
    STORAGE_PATH: string = process.env.STORAGE_PATH as string;

    constructor(@inject(TYPES.DatabaseService) private databaseService: DatabaseService) {
        this.upload = this.databaseService.createMulterUpload(STORAGE_DIR);
        this.configureRouter();
        this.databaseService.start();
    }

    private configureRouter(): void {
        this.router = Router();

        /**
         * @swagger
         *
         * definitions:
         *   MetaData:
         *     type: object
         *     properties:
         *       name:
         *         type: string
         *       tags:
         *         type: string[]
         *       filename:
         *         type: string
         *       ext:
         *         type: string
         */

        /**
         * @swagger
         *
         * /api/database/metaDatas:
         *   get:
         *     description: Get all metadatas of database
         *     tags:
         *       - Get MetaDatas
         *     produces:
         *       - application/json
         *     responses:
         *       200:
         *         schema:
         *           $ref: '#/definitions/MetaData'
         *       404:
         *         schema:
         *           $ref: '#/definitions/MetaData'
         */
        /**
         * @swagger
         *
         * /api/database/getDrawing:
         *   get:
         *     description: Return drawing
         *     parameters:
         *        - in: path
         *          name: filename
         *          type: string
         *     tags:
         *       - GetDrawing
         *     produces:
         *       - application/json
         *     responses:
         *       200:
         *         schema:
         *           $ref: '#/definitions/MetaData'
         *       400:
         *         schema:
         *           $ref: '#/definitions/MetaData'
         */

        this.router.get('/metaDatas', (req: Request, res: Response, next: NextFunction) => {
            this.databaseService
                .getMetaData()
                .then((metaData: MetaData[]) => {
                    res.json(metaData).status(HttpStatus.OK);
                })
                .catch((error: Error) => {
                    res.status(HttpStatus.NOT_FOUND).send(error.message);
                });
        }); 

        this.router.post('/addDrawing', this.upload.single('image'), (req: Request, res: Response, next: NextFunction) => {
            req.body['filename'] = req.file.filename;
            let tags: string[];
            !req.body['tags'] ? (tags = Array(0)) : (tags = req.body['tags']);
            if (!Array.isArray(tags)) tags = [tags];
            const meta: MetaData = { name: req.body['name'], tags: tags, filename: req.body['filename'], ext: req.body['ext'] };
            this.databaseService
                .addDrawing(meta)
                .then(() => {
                    res.sendStatus(HttpStatus.NO_CONTENT);
                })
                .catch((error: Error) => {
                    res.status(HttpStatus.NOT_FOUND).send(error.message);
                });
        });
 
        this.router.get('/getDrawing/:filename', (req: Request, res: Response, next: NextFunction) => {
            this.databaseService.localDrawings = fs.readdirSync(this.databaseService.getDirPath());
            if (this.databaseService.localDrawings.includes(req.params.filename)) {
                res.sendFile(req.params.filename, { root: path.join(__dirname, this.databaseService.getImagePath()) });
                res.status(HttpStatus.OK).contentType('image/png');
            } else {
                const error = new Error('Aucun dessin trouvÃ©');
                res.status(HttpStatus.NOT_FOUND).send(error.message);
            }
        });

        this.router.delete('/deleteDrawing/:filename', (req: Request, res: Response, next: NextFunction) => {
            this.databaseService
                .deleteDrawing(req.params.filename)
                .then(() => {
                    res.sendStatus(HttpStatus.NO_CONTENT).status(HttpStatus.NO_CONTENT);
                })
                .catch((error: Error) => {
                    res.status(HttpStatus.NOT_FOUND).send(error.message);
                });
        }); 
    } 
}
